"use client";

import React, { useState, useEffect } from "react";
import { Input } from "@/components/ui/input";
import { Button } from "@/components/ui/button";
import { Label } from "@/components/ui/label";
import { Card, CardHeader, CardTitle, CardContent } from "@/components/ui/card";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogTrigger } from "@/components/ui/dialog";

interface Supply {
  id: string;
  name: string;
  quantity: string;
  unitCost: string;
  unit: string;
}

interface SavedProduct {
  id: string;
  name: string;
  description: string;
  supplies: Supply[];
  laborHours: string;
  hourlyRate: string;
  suppliesCost: number;
  laborCost: number;
  totalCost: number;
  margin: string;
  finalPrice: number;
  createdAt: string;
  updatedAt: string;
}

interface SavedSupply {
  id: string;
  name: string;
  unit: string;
  unitCost: string;
  category?: string;
  createdAt: string;
  updatedAt: string;
}

function PricingForm() {
  // States for supplies and labor
  const [supplies, setSupplies] = useState<Supply[]>([]);
  const [laborHours, setLaborHours] = useState("");
  const [hourlyRate, setHourlyRate] = useState("");
  
  // States for final pricing
  const [manualCost, setManualCost] = useState("");
  const [useCalculatedCost, setUseCalculatedCost] = useState(true);
  const [margin, setMargin] = useState("");
  const [price, setPrice] = useState<number | null>(null);
  const [error, setError] = useState("");
  
  // Calculated costs
  const [suppliesCost, setSuppliesCost] = useState(0);
  const [laborCost, setLaborCost] = useState(0);
  const [totalCalculatedCost, setTotalCalculatedCost] = useState(0);

  // States for saving products
  const [savedProducts, setSavedProducts] = useState<SavedProduct[]>([]);
  const [productName, setProductName] = useState("");
  const [productDescription, setProductDescription] = useState("");
  const [showSaveDialog, setShowSaveDialog] = useState(false);
  const [currentEditingProduct, setCurrentEditingProduct] = useState<string | null>(null);

  // States for saving supplies
  const [savedSupplies, setSavedSupplies] = useState<SavedSupply[]>([]);
  const [showSupplyDialog, setShowSupplyDialog] = useState(false);
  const [supplyName, setSupplyName] = useState("");
  const [supplyUnit, setSupplyUnit] = useState("");
  const [supplyUnitCost, setSupplyUnitCost] = useState("");
  const [supplyCategory, setSupplyCategory] = useState("");
  const [searchSupply, setSearchSupply] = useState("");
  const [showSupplySearch, setShowSupplySearch] = useState(false);

  // Load saved products and supplies from localStorage on component mount
  useEffect(() => {
    const savedProductsData = localStorage.getItem('savedProducts');
    if (savedProductsData) {
      try {
        setSavedProducts(JSON.parse(savedProductsData));
      } catch (error) {
        console.error('Error loading saved products:', error);
      }
    }

    const savedSuppliesData = localStorage.getItem('savedSupplies');
    if (savedSuppliesData) {
      try {
        setSavedSupplies(JSON.parse(savedSuppliesData));
      } catch (error) {
        console.error('Error loading saved supplies:', error);
      }
    }
  }, []);

  // Calculate costs whenever supplies or labor change
  useEffect(() => {
    const suppliesTotal = supplies.reduce((total, supply) => {
      const quantity = parseFloat(supply.quantity) || 0;
      const unitCost = parseFloat(supply.unitCost) || 0;
      return total + (quantity * unitCost);
    }, 0);
    
    const laborTotal = (parseFloat(laborHours) || 0) * (parseFloat(hourlyRate) || 0);
    const totalCost = suppliesTotal + laborTotal;
    
    setSuppliesCost(suppliesTotal);
    setLaborCost(laborTotal);
    setTotalCalculatedCost(totalCost);
  }, [supplies, laborHours, hourlyRate]);

  // Save products to localStorage
  const saveToLocalStorage = (products: SavedProduct[]) => {
    try {
      localStorage.setItem('savedProducts', JSON.stringify(products));
    } catch (error) {
      console.error('Error saving products:', error);
    }
  };

  // Add new supply
  const addSupply = () => {
    const newSupply: Supply = {
      id: Date.now().toString(),
      name: "",
      quantity: "",
      unitCost: "",
      unit: "un"
    };
    setSupplies([...supplies, newSupply]);
  };

  // Update supply
  const updateSupply = (id: string, field: keyof Supply, value: string) => {
    setSupplies(supplies.map(supply => 
      supply.id === id ? { ...supply, [field]: value } : supply
    ));
  };

  // Remove supply
  const removeSupply = (id: string) => {
    setSupplies(supplies.filter(supply => supply.id !== id));
  };

  // Save current product
  const saveProduct = () => {
    if (!productName.trim()) {
      setError("Por favor, insira um nome para o produto.");
      return;
    }

    const now = new Date().toISOString();
    const productData: SavedProduct = {
      id: currentEditingProduct || Date.now().toString(),
      name: productName.trim(),
      description: productDescription.trim(),
      supplies: supplies,
      laborHours,
      hourlyRate,
      suppliesCost,
      laborCost,
      totalCost: totalCalculatedCost,
      margin,
      finalPrice: price || 0,
      createdAt: currentEditingProduct ? 
        savedProducts.find(p => p.id === currentEditingProduct)?.createdAt || now : now,
      updatedAt: now
    };

    let updatedProducts;
    if (currentEditingProduct) {
      updatedProducts = savedProducts.map(p => 
        p.id === currentEditingProduct ? productData : p
      );
    } else {
      updatedProducts = [...savedProducts, productData];
    }

    setSavedProducts(updatedProducts);
    saveToLocalStorage(updatedProducts);
    setShowSaveDialog(false);
    setProductName("");
    setProductDescription("");
    setCurrentEditingProduct(null);
    setError("");
  };

  // Load product into calculator
  const loadProduct = (product: SavedProduct) => {
    setSupplies(product.supplies);
    setLaborHours(product.laborHours);
    setHourlyRate(product.hourlyRate);
    setMargin(product.margin);
    setPrice(product.finalPrice);
    setUseCalculatedCost(true);
    setCurrentEditingProduct(product.id);
  };

  // Delete product
  const deleteProduct = (productId: string) => {
    const updatedProducts = savedProducts.filter(p => p.id !== productId);
    setSavedProducts(updatedProducts);
    saveToLocalStorage(updatedProducts);
    
    if (currentEditingProduct === productId) {
      setCurrentEditingProduct(null);
    }
  };

  // Duplicate product
  const duplicateProduct = (product: SavedProduct) => {
    const duplicatedProduct: SavedProduct = {
      ...product,
      id: Date.now().toString(),
      name: `${product.name} (C√≥pia)`,
      createdAt: new Date().toISOString(),
      updatedAt: new Date().toISOString()
    };

    const updatedProducts = [...savedProducts, duplicatedProduct];
    setSavedProducts(updatedProducts);
    saveToLocalStorage(updatedProducts);
  };

  // Save supplies to localStorage
  const saveSupplesToLocalStorage = (supplies: SavedSupply[]) => {
    try {
      localStorage.setItem('savedSupplies', JSON.stringify(supplies));
    } catch (error) {
      console.error('Error saving supplies:', error);
    }
  };

  // Save new supply
  const saveSupply = () => {
    if (!supplyName.trim()) {
      setError("Por favor, insira um nome para o insumo.");
      return;
    }

    const now = new Date().toISOString();
    const supplyData: SavedSupply = {
      id: Date.now().toString(),
      name: supplyName.trim(),
      unit: supplyUnit.trim() || "un",
      unitCost: supplyUnitCost,
      category: supplyCategory.trim(),
      createdAt: now,
      updatedAt: now
    };

    const updatedSupplies = [...savedSupplies, supplyData];
    setSavedSupplies(updatedSupplies);
    saveSupplesToLocalStorage(updatedSupplies);
    setShowSupplyDialog(false);
    setSupplyName("");
    setSupplyUnit("");
    setSupplyUnitCost("");
    setSupplyCategory("");
    setError("");
  };

  // Add saved supply to current supplies
  const addSavedSupply = (savedSupply: SavedSupply, quantity: string = "1") => {
    const newSupply: Supply = {
      id: Date.now().toString(),
      name: savedSupply.name,
      quantity: quantity,
      unitCost: savedSupply.unitCost,
      unit: savedSupply.unit
    };
    setSupplies([...supplies, newSupply]);
    setShowSupplySearch(false);
    setSearchSupply("");
  };

  // Delete saved supply
  const deleteSavedSupply = (supplyId: string) => {
    const updatedSupplies = savedSupplies.filter(s => s.id !== supplyId);
    setSavedSupplies(updatedSupplies);
    saveSupplesToLocalStorage(updatedSupplies);
  };

  // Filter supplies based on search
  const filteredSupplies = savedSupplies.filter(supply =>
    supply.name.toLowerCase().includes(searchSupply.toLowerCase()) ||
    (supply.category && supply.category.toLowerCase().includes(searchSupply.toLowerCase()))
  );

  // Calculate final selling price
  const handleCalculate = () => {
    setError("");
    const costValue = useCalculatedCost ? totalCalculatedCost : parseFloat(manualCost);
    const marginValue = parseFloat(margin);

    // Validate numeric values
    if (isNaN(costValue) || isNaN(marginValue)) {
      setError("Por favor, insira valores num√©ricos v√°lidos para custo e margem.");
      setPrice(null);
      return;
    }

    if (costValue < 0 || marginValue < 0) {
      setError("Os valores devem ser positivos.");
      setPrice(null);
      return;
    }

    // Calculation: final price = cost * (1 + margin/100)
    const calculatedPrice = costValue * (1 + marginValue / 100);
    setPrice(calculatedPrice);
  };

  const handleReset = () => {
    setSupplies([]);
    setLaborHours("");
    setHourlyRate("");
    setManualCost("");
    setMargin("");
    setPrice(null);
    setError("");
    setUseCalculatedCost(true);
    setCurrentEditingProduct(null);
    setProductName("");
    setProductDescription("");
  };

  return (
    <div className="space-y-6">
      <Tabs defaultValue="cost-calculation" className="w-full">
        <TabsList className="grid w-full grid-cols-4">
          <TabsTrigger value="cost-calculation">C√°lculo de Custo</TabsTrigger>
          <TabsTrigger value="final-pricing">Precifica√ß√£o Final</TabsTrigger>
          <TabsTrigger value="saved-products">Produtos Salvos ({savedProducts.length})</TabsTrigger>
          <TabsTrigger value="supply-library">Biblioteca de Insumos ({savedSupplies.length})</TabsTrigger>
        </TabsList>
        
        <TabsContent value="cost-calculation" className="space-y-6">
          {/* Supplies Section */}
          <Card>
            <CardHeader>
              <CardTitle className="text-lg">Insumos e Materiais</CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              {supplies.map((supply) => (
                <div key={supply.id} className="grid grid-cols-12 gap-2 items-end">
                  <div className="col-span-4">
                    <Label className="text-xs">Nome do Insumo</Label>
                    <Input
                      placeholder="Ex: Farinha"
                      value={supply.name}
                      onChange={(e) => updateSupply(supply.id, "name", e.target.value)}
                    />
                  </div>
                  <div className="col-span-2">
                    <Label className="text-xs">Qtd</Label>
                    <Input
                      type="number"
                      step="0.01"
                      placeholder="1.5"
                      value={supply.quantity}
                      onChange={(e) => updateSupply(supply.id, "quantity", e.target.value)}
                    />
                  </div>
                  <div className="col-span-2">
                    <Label className="text-xs">Unidade</Label>
                    <Input
                      placeholder="kg"
                      value={supply.unit}
                      onChange={(e) => updateSupply(supply.id, "unit", e.target.value)}
                    />
                  </div>
                  <div className="col-span-3">
                    <Label className="text-xs">Custo Unit. (R$)</Label>
                    <Input
                      type="number"
                      step="0.01"
                      placeholder="5.00"
                      value={supply.unitCost}
                      onChange={(e) => updateSupply(supply.id, "unitCost", e.target.value)}
                    />
                  </div>
                  <div className="col-span-1">
                    <Button
                      variant="outline"
                      size="sm"
                      onClick={() => removeSupply(supply.id)}
                      className="text-red-600 hover:text-red-700"
                    >
                      √ó
                    </Button>
                  </div>
                </div>
              ))}
              
              <div className="flex gap-2">
                <Button onClick={addSupply} variant="outline" className="flex-1">
                  + Adicionar Insumo
                </Button>
                <Dialog open={showSupplySearch} onOpenChange={setShowSupplySearch}>
                  <DialogTrigger asChild>
                    <Button variant="outline" className="flex-1">
                      üîç Buscar Insumos Salvos
                    </Button>
                  </DialogTrigger>
                  <DialogContent>
                    <DialogHeader>
                      <DialogTitle>Buscar Insumos Salvos</DialogTitle>
                    </DialogHeader>
                    <div className="space-y-4">
                      <Input
                        placeholder="Pesquisar por nome ou categoria..."
                        value={searchSupply}
                        onChange={(e) => setSearchSupply(e.target.value)}
                      />
                      
                      {filteredSupplies.length === 0 ? (
                        <div className="text-center text-muted-foreground py-4">
                          {searchSupply ? "Nenhum insumo encontrado" : "Nenhum insumo salvo ainda"}
                        </div>
                      ) : (
                        <div className="max-h-60 overflow-y-auto space-y-2">
                          {filteredSupplies.map((supply) => (
                            <div key={supply.id} className="flex items-center justify-between p-3 border rounded-lg">
                              <div className="flex-1">
                                <div className="font-medium">{supply.name}</div>
                                <div className="text-sm text-muted-foreground">
                                  R$ {supply.unitCost}/{supply.unit}
                                  {supply.category && ` ‚Ä¢ ${supply.category}`}
                                </div>
                              </div>
                              <div className="flex gap-2">
                                <Input
                                  type="number"
                                  placeholder="Qtd"
                                  className="w-20"
                                  id={`qty-${supply.id}`}
                                  step="0.01"
                                />
                                <Button
                                  size="sm"
                                  onClick={() => {
                                    const qtyInput = document.getElementById(`qty-${supply.id}`) as HTMLInputElement;
                                    const quantity = qtyInput?.value || "1";
                                    addSavedSupply(supply, quantity);
                                  }}
                                >
                                  Adicionar
                                </Button>
                              </div>
                            </div>
                          ))}
                        </div>
                      )}
                    </div>
                  </DialogContent>
                </Dialog>
              </div>
              
              {supplies.length > 0 && (
                <div className="text-right text-sm font-medium">
                  Total Insumos: R$ {suppliesCost.toFixed(2)}
                </div>
              )}
            </CardContent>
          </Card>

          {/* Labor Section */}
          <Card>
            <CardHeader>
              <CardTitle className="text-lg">M√£o de Obra</CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              <div className="grid grid-cols-2 gap-4">
                <div>
                  <Label htmlFor="labor-hours" className="text-sm font-medium">
                    Horas Trabalhadas
                  </Label>
                  <Input
                    id="labor-hours"
                    type="number"
                    step="0.1"
                    placeholder="Ex: 2.5"
                    value={laborHours}
                    onChange={(e) => setLaborHours(e.target.value)}
                  />
                </div>
                <div>
                  <Label htmlFor="hourly-rate" className="text-sm font-medium">
                    Valor por Hora (R$)
                  </Label>
                  <Input
                    id="hourly-rate"
                    type="number"
                    step="0.01"
                    placeholder="Ex: 25.00"
                    value={hourlyRate}
                    onChange={(e) => setHourlyRate(e.target.value)}
                  />
                </div>
              </div>
              
              {(laborHours || hourlyRate) && (
                <div className="text-right text-sm font-medium">
                  Total M√£o de Obra: R$ {laborCost.toFixed(2)}
                </div>
              )}
            </CardContent>
          </Card>

          {/* Cost Summary */}
          {(supplies.length > 0 || laborHours || hourlyRate) && (
            <Card className="bg-blue-50 dark:bg-blue-950/20 border-blue-200 dark:border-blue-800">
              <CardContent className="pt-6">
                <div className="space-y-2 text-sm">
                  <div className="flex justify-between">
                    <span>Custo dos Insumos:</span>
                    <span>R$ {suppliesCost.toFixed(2)}</span>
                  </div>
                  <div className="flex justify-between">
                    <span>Custo da M√£o de Obra:</span>
                    <span>R$ {laborCost.toFixed(2)}</span>
                  </div>
                  <div className="flex justify-between font-bold text-base border-t pt-2">
                    <span>Custo Total Calculado:</span>
                    <span>R$ {totalCalculatedCost.toFixed(2)}</span>
                  </div>
                </div>
              </CardContent>
            </Card>
          )}
        </TabsContent>

        <TabsContent value="final-pricing" className="space-y-6">
          {/* Cost Selection */}
          <Card>
            <CardHeader>
              <CardTitle className="text-lg">Custo do Produto</CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              <div className="space-y-3">
                <div className="flex items-center space-x-2">
                  <input
                    type="radio"
                    id="use-calculated"
                    checked={useCalculatedCost}
                    onChange={() => setUseCalculatedCost(true)}
                  />
                  <Label htmlFor="use-calculated" className="text-sm">
                    Usar custo calculado: R$ {totalCalculatedCost.toFixed(2)}
                  </Label>
                </div>
                
                <div className="flex items-center space-x-2">
                  <input
                    type="radio"
                    id="use-manual"
                    checked={!useCalculatedCost}
                    onChange={() => setUseCalculatedCost(false)}
                  />
                  <Label htmlFor="use-manual" className="text-sm">
                    Inserir custo manualmente
                  </Label>
                </div>
                
                {!useCalculatedCost && (
                  <Input
                    type="number"
                    step="0.01"
                    placeholder="Ex: 50.00"
                    value={manualCost}
                    onChange={(e) => setManualCost(e.target.value)}
                    className="mt-2"
                  />
                )}
              </div>
            </CardContent>
          </Card>

          {/* Margin and Final Calculation */}
          <div className="flex flex-col">
            <Label htmlFor="margin" className="mb-2 text-sm font-medium">
              Margem de Lucro (%)
            </Label>
            <Input
              id="margin"
              type="number"
              step="0.1"
              min="0"
              placeholder="Ex: 30"
              value={margin}
              onChange={(e) => setMargin(e.target.value)}
              aria-invalid={!!error}
            />
          </div>

          {error && (
            <div className="text-sm text-red-600 bg-red-50 dark:bg-red-950/20 p-3 rounded-md border border-red-200 dark:border-red-800">
              {error}
            </div>
          )}

          <div className="flex gap-3">
            <Button onClick={handleCalculate} className="flex-1">
              Calcular Pre√ßo de Venda
            </Button>
            <Dialog open={showSaveDialog} onOpenChange={setShowSaveDialog}>
              <DialogTrigger asChild>
                <Button variant="outline" className="flex-1" disabled={!price}>
                  {currentEditingProduct ? "Atualizar Produto" : "Salvar Produto"}
                </Button>
              </DialogTrigger>
              <DialogContent>
                <DialogHeader>
                  <DialogTitle>
                    {currentEditingProduct ? "Atualizar Produto" : "Salvar Produto"}
                  </DialogTitle>
                </DialogHeader>
                <div className="space-y-4">
                  <div>
                    <Label htmlFor="product-name" className="text-sm font-medium">
                      Nome do Produto *
                    </Label>
                    <Input
                      id="product-name"
                      placeholder="Ex: Bolo de Chocolate"
                      value={productName}
                      onChange={(e) => setProductName(e.target.value)}
                    />
                  </div>
                  <div>
                    <Label htmlFor="product-description" className="text-sm font-medium">
                      Descri√ß√£o (opcional)
                    </Label>
                    <Input
                      id="product-description"
                      placeholder="Ex: Bolo de chocolate com cobertura"
                      value={productDescription}
                      onChange={(e) => setProductDescription(e.target.value)}
                    />
                  </div>
                  <div className="flex gap-3">
                    <Button onClick={saveProduct} className="flex-1">
                      {currentEditingProduct ? "Atualizar" : "Salvar"}
                    </Button>
                    <Button 
                      variant="outline" 
                      onClick={() => setShowSaveDialog(false)}
                      className="flex-1"
                    >
                      Cancelar
                    </Button>
                  </div>
                </div>
              </DialogContent>
            </Dialog>
            <Button onClick={handleReset} variant="outline" className="flex-1">
              Limpar Tudo
            </Button>
          </div>

          {price !== null && (
            <div className="mt-6 rounded-lg border border-green-200 dark:border-green-800 p-4 bg-green-50 dark:bg-green-950/20">
              <div className="text-center">
                <span className="block text-sm font-medium text-green-800 dark:text-green-200 mb-1">
                  Pre√ßo de Venda Sugerido
                </span>
                <span className="block text-2xl font-bold text-green-900 dark:text-green-100">
                  R$ {price.toFixed(2)}
                </span>
                <div className="mt-3 text-xs text-green-700 dark:text-green-300 space-y-1">
                  <div>Custo: R$ {(useCalculatedCost ? totalCalculatedCost : parseFloat(manualCost) || 0).toFixed(2)}</div>
                  <div>Margem: {parseFloat(margin || "0").toFixed(1)}%</div>
                  <div>Lucro: R$ {(price - (useCalculatedCost ? totalCalculatedCost : parseFloat(manualCost) || 0)).toFixed(2)}</div>
                </div>
              </div>
            </div>
          )}
        </TabsContent>

        <TabsContent value="saved-products" className="space-y-6">
          {savedProducts.length === 0 ? (
            <Card>
              <CardContent className="pt-6">
                <div className="text-center text-muted-foreground">
                  <p className="text-lg mb-2">Nenhum produto salvo ainda</p>
                  <p className="text-sm">
                    Calcule e salve seus produtos para reutilizar os c√°lculos depois
                  </p>
                </div>
              </CardContent>
            </Card>
          ) : (
            <div className="grid gap-4">
              {savedProducts.map((product) => (
                <Card key={product.id} className="relative">
                  <CardHeader>
                    <div className="flex justify-between items-start">
                      <div>
                        <CardTitle className="text-lg">{product.name}</CardTitle>
                        {product.description && (
                          <p className="text-sm text-muted-foreground mt-1">
                            {product.description}
                          </p>
                        )}
                      </div>
                      <div className="text-right">
                        <div className="text-lg font-bold text-green-600">
                          R$ {product.finalPrice.toFixed(2)}
                        </div>
                        <div className="text-xs text-muted-foreground">
                          Custo: R$ {product.totalCost.toFixed(2)}
                        </div>
                      </div>
                    </div>
                  </CardHeader>
                  <CardContent>
                    <div className="grid grid-cols-2 gap-4 text-sm">
                      <div>
                        <strong>Insumos:</strong> {product.supplies.length} itens
                        <div className="text-xs text-muted-foreground mt-1">
                          {product.supplies.slice(0, 2).map(s => s.name).join(", ")}
                          {product.supplies.length > 2 && "..."}
                        </div>
                      </div>
                      <div>
                        <strong>M√£o de Obra:</strong> {product.laborHours}h
                        <div className="text-xs text-muted-foreground mt-1">
                          R$ {product.hourlyRate}/hora
                        </div>
                      </div>
                    </div>
                    
                    <div className="flex gap-2 mt-4">
                      <Button
                        size="sm"
                        onClick={() => loadProduct(product)}
                        className="flex-1"
                      >
                        Carregar
                      </Button>
                      <Button
                        size="sm"
                        variant="outline"
                        onClick={() => duplicateProduct(product)}
                        className="flex-1"
                      >
                        Duplicar
                      </Button>
                      <Button
                        size="sm"
                        variant="outline"
                        onClick={() => deleteProduct(product.id)}
                        className="text-red-600 hover:text-red-700"
                      >
                        Excluir
                      </Button>
                    </div>
                    
                    <div className="text-xs text-muted-foreground mt-2 border-t pt-2">
                      Criado: {new Date(product.createdAt).toLocaleDateString('pt-BR')}
                      {product.updatedAt !== product.createdAt && (
                        <span> ‚Ä¢ Atualizado: {new Date(product.updatedAt).toLocaleDateString('pt-BR')}</span>
                      )}
                    </div>
                  </CardContent>
                </Card>
              ))}
            </div>
          )}
        </TabsContent>

        <TabsContent value="supply-library" className="space-y-6">
          <div className="flex justify-between items-center">
            <h3 className="text-lg font-semibold">Biblioteca de Insumos</h3>
            <Dialog open={showSupplyDialog} onOpenChange={setShowSupplyDialog}>
              <DialogTrigger asChild>
                <Button>+ Adicionar Insumo</Button>
              </DialogTrigger>
              <DialogContent>
                <DialogHeader>
                  <DialogTitle>Adicionar Novo Insumo</DialogTitle>
                </DialogHeader>
                <div className="space-y-4">
                  <div>
                    <Label htmlFor="supply-name" className="text-sm font-medium">
                      Nome do Insumo *
                    </Label>
                    <Input
                      id="supply-name"
                      placeholder="Ex: Farinha de Trigo"
                      value={supplyName}
                      onChange={(e) => setSupplyName(e.target.value)}
                    />
                  </div>
                  <div className="grid grid-cols-2 gap-4">
                    <div>
                      <Label htmlFor="supply-unit" className="text-sm font-medium">
                        Unidade
                      </Label>
                      <Input
                        id="supply-unit"
                        placeholder="Ex: kg"
                        value={supplyUnit}
                        onChange={(e) => setSupplyUnit(e.target.value)}
                      />
                    </div>
                    <div>
                      <Label htmlFor="supply-cost" className="text-sm font-medium">
                        Custo Unit√°rio (R$) *
                      </Label>
                      <Input
                        id="supply-cost"
                        type="number"
                        step="0.01"
                        placeholder="Ex: 4.50"
                        value={supplyUnitCost}
                        onChange={(e) => setSupplyUnitCost(e.target.value)}
                      />
                    </div>
                  </div>
                  <div>
                    <Label htmlFor="supply-category" className="text-sm font-medium">
                      Categoria (opcional)
                    </Label>
                    <Input
                      id="supply-category"
                      placeholder="Ex: Farinhas, Temperos, etc."
                      value={supplyCategory}
                      onChange={(e) => setSupplyCategory(e.target.value)}
                    />
                  </div>
                  <div className="flex gap-3">
                    <Button onClick={saveSupply} className="flex-1">
                      Salvar Insumo
                    </Button>
                    <Button 
                      variant="outline" 
                      onClick={() => setShowSupplyDialog(false)}
                      className="flex-1"
                    >
                      Cancelar
                    </Button>
                  </div>
                </div>
              </DialogContent>
            </Dialog>
          </div>

          {savedSupplies.length === 0 ? (
            <Card>
              <CardContent className="pt-6">
                <div className="text-center text-muted-foreground">
                  <p className="text-lg mb-2">Nenhum insumo salvo ainda</p>
                  <p className="text-sm">
                    Adicione insumos √† sua biblioteca para reutilizar em diferentes produtos
                  </p>
                </div>
              </CardContent>
            </Card>
          ) : (
            <div className="grid gap-4">
              {savedSupplies.map((supply) => (
                <Card key={supply.id}>
                  <CardContent className="pt-4">
                    <div className="flex justify-between items-start">
                      <div className="flex-1">
                        <h4 className="font-semibold text-lg">{supply.name}</h4>
                        <div className="text-sm text-muted-foreground mt-1">
                          <span className="font-medium">R$ {supply.unitCost}/{supply.unit}</span>
                          {supply.category && (
                            <span className="ml-2 px-2 py-1 bg-blue-100 dark:bg-blue-900 text-blue-800 dark:text-blue-200 rounded-full text-xs">
                              {supply.category}
                            </span>
                          )}
                        </div>
                        <div className="text-xs text-muted-foreground mt-2">
                          Criado: {new Date(supply.createdAt).toLocaleDateString('pt-BR')}
                        </div>
                      </div>
                      <div className="flex gap-2">
                        <Button
                          size="sm"
                          variant="outline"
                          onClick={() => addSavedSupply(supply)}
                        >
                          Usar no C√°lculo
                        </Button>
                        <Button
                          size="sm"
                          variant="outline"
                          onClick={() => deleteSavedSupply(supply.id)}
                          className="text-red-600 hover:text-red-700"
                        >
                          Excluir
                        </Button>
                      </div>
                    </div>
                  </CardContent>
                </Card>
              ))}
            </div>
          )}
        </TabsContent>
      </Tabs>
    </div>
  );
}

export default PricingForm;
